<template>
    <div class="historical-video-page">
      <div class="analysis-object-tree-warp">
        <!--<water-radio-tree></water-radio-tree>-->
      </div>
      <div class="secondary-page-mode">
        <div class="secondary-toolbar">
          <span>时间段：</span>
          <el-date-picker
            v-model = "dataValue"
            type = "daterange"
            align = "left"
            unlink-panels
            range-separator = "~"
            start-placeholder = "开始日期"
            end-placeholder = "结束日期"
            :clearable = 'false'
            format = 'yyyy-MM-dd HH:mm'
            prefix-icon = 'el-icon-date'
            :picker-options="pickerOptions">
          </el-date-picker>&emsp;
          <button type="button" class="default-btn">
            <i class="iconfont icon-chaxun"></i> 查询
          </button>
        </div>
        <div class="currency-mode-wrap">
          <h4 class="currency-mode-title">
            <i class="mode-title-icon"></i> 庄行站
            <span class="mode-title-line"></span>
          </h4>
          <div></div>
          <div class="video-content-wrap">
            <ul class="video-list clear">
              <li v-for="item,index in smallList">
                <div class="small-img-box">
                  <img :src="item.src" :alt="item.name">
                </div>
                <p class="small-img-title">{{ item.name }}</p>
              </li>
            </ul>
          </div>
          <!-- 分页 -->
          <div class="table-pagination-wrap">
            <el-pagination
              @size-change="paginationSizeChange"
              @current-change="paginationChange"
              :current-page.sync="currentPage"
              :page-size="pageSize"
              background
              :small = "true"
              popper-class = "select-common-option"
              :page-sizes = "pageList"
              layout="total, sizes, slot ,->,prev, pager, next, jumper"
              :total="total">
              <span class="el-pagination__current">当前第{{ currentPage }}/{{totalPageNumber}}页</span>
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
  // import waterRadioTree from '@/components/currency/waterRadioTree'
  let pickerOptions = {
    shortcuts: [{
      text: '最近一周',
      onClick(picker) {
        const end = new Date();
        const start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
        picker.$emit('pick', [start, end]);
      }
    }, {
      text: '最近一个月',
      onClick(picker) {
        const end = new Date();
        const start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
        picker.$emit('pick', [start, end]);
      }
    }, {
      text: '最近三个月',
      onClick(picker) {
        const end = new Date();
        const start = new Date();
        start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
        picker.$emit('pick', [start, end]);
      }
    }]
  };
  let smallList = [{
    src:'/static/videoList/imageB.png',
    name:'2018-07-12 12:00'
  },{
    src:'/static/videoList/imageC.png',
    name:'2018-07-12 12:00'
  },{
    src:'/static/videoList/imageD.png',
    name:'2018-07-12 12:00'
  },{
    src:'/static/videoList/imageB.png',
    name:'2018-07-12 12:00'
  },{
    src:'/static/videoList/imageC.png',
    name:'2018-07-12 12:00'
  },{
    src:'/static/videoList/imageD.png',
    name:'2018-07-12 12:00'
  }];
  export default {
      name: "historicalVideo",
      data(){
         return {
           pickerOptions,
           dataValue:'',
           smallList,
           currentPage:1,//分页组件当前页
           total: 62,//分页总条数
           pageSize: 10,//分页每页多少条数据
           pageList:[10,15,20],//设置每页多少条
         }
      },
      computed:{
        totalPageNumber(){//计算总页数
          let number = 0;
          number = Math.ceil(this.total/this.pageSize);
          return number;
        }
      },
      methods:{
        paginationChange(val){//分页页码改变时执行
        },
        paginationSizeChange(val){//分页pageSize改变时执行
          this.pageSize = val;
        },
      },
      components:{
        // waterRadioTree
      }
  }
</script>

<style scoped lang="less">
.historical-video-page{
  height: 100%;
  .icon-chaxun{
    font-size: 14px;
  }
  .currency-mode-wrap{
    height: calc(100% - 56px);

  }
  .video-content-wrap{
    height: calc(100% - 74px);
    border-bottom: 1px solid #d9e6f7;
  }
  .video-list{
    padding: 4px;
    text-align: center;
    height: 100%;
    li{
      width: calc(25% - 8px);
      height: calc(33.3% - 8px);
      float: left;
      margin: 4px;
      cursor: pointer;
      .small-img-box{
        height: calc(100% - 30px);
        overflow: hidden;
        img{
          height: 100%;
          width: 100%;
          transition: all 0.2s;
        }
      }
      .small-img-title{
        height: 30px;
        line-height: 30px;
        text-align: center;
      }
    }
    li:hover{
      background: #eee;
      img{
        transform: scale(1.4);
      }
    }
  }
}
#app .historical-video-page .table-pagination-wrap{
  border: none;
}
</style>



